<template>
  <div>
    <single-imageB :selectedImageB=" selectedImageB "></single-imageB>
    <div class="row">
        <div v-for="imagesB in imagesB" :key="imagesB.id" class="col-md-3 col-sm-4 col-6"><a href="#tituloPto" v-on:click="showImage(imagesB)"><img class ="img-fluid"  v-bind:src="imagesB.thumbnail" /></a></div>
    </div>
  </div>
</template>
<script>
import SingleImageB from './SingleImageB'
const $ = require('jquery')
window.$ = $
export default{
    props:['imagesB'],
    components:{SingleImageB},
    data(){
        return{
            selectedImageB:'',
        }
    },
  methods:{
    showImage:function (imagesB){
      this.selectedImageB= imagesB
       $('#infopto').show();
    }
  }
}
</script>
