<template>
  <div>
    <single-image :selectedImage=" selectedImage "></single-image>
    <div class="row">
        <div v-for="images in images" :key="images.id" class="col-md-3 col-sm-4 col-6"><a class="linkbib" href="#tituloBiblio"  v-on:click="showImage(images)"><img class ="img-fluid" id="imggal" v-bind:src="images.thumbnail" /></a></div>
    </div>
  </div>
</template>
<script>
import SingleImage from './SingleImage'
const $ = require('jquery')
window.$ = $
export default{
    props:['images'],
    components:{SingleImage},
    data(){
        return{
            selectedImage:'',
        }
    },
  methods:{
    showImage:function (images){
      this.selectedImage= images
      $('#infobib').show();
      this.$router.push('/bibliotecas');
    }
  }
}
</script>

