<template>
    <div>
        <ul>
            <li v-for="item of Bibliotecas" :key=item.id>
                {{item.id}}-{{item.Title}} - {{item.direccion}}- {{item.thumbnail}}- {{item.Telefono}}
            </li>
        </ul>
        {{Bibliotecas}}
    </div>
</template>

<script>
import {mapActions, mapState} from 'vuex'
export default {
    name: 'fir',
    methods:{
        ...mapActions(['getBibliotecas'])
    },
    created(){
        this.getBibliotecas()
    },
    computed:{
        ...mapState(['Bibliotecas'])
    }
}
</script>

